.nav {
    position: fixed;
    top: 80px;
    width: 300px;
    background-color: rgba(50, 50, 50, 0.9);
    color: whitesmoke;
    border-radius: 20px;
    box-shadow: 0px 0px 5px black;
}

.item {
    height: 50px;
    border-radius: 20px;
    margin: 5px;
    background-color: transparent;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 25px;
}

.item a {
    display: block;
    height: 50px;
    line-height: 50px;
    text-align: left;
    color: whitesmoke;
    text-decoration: none;
    padding-left: 20px;
}

a.activeLink {
    color: #328ECC;
}

.item_indicator {
    float: right;
    height: 30px;
    line-height: 30px;
    min-width: 20px;
    max-width: 100px;
    margin: 5px;
    padding: 5px;
    background-color: #2E8FCD;
     /* #007ACC; */
    border-radius: 20px;
    font-size: 15px;
    color: whitesmoke;
}

.item:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

@media only screen and (max-width: 720px) {
    .nav {
        position: static;
        padding-top: 70px;
        padding-bottom: 1px;
        width: 100%;
    }
}